/* global anime jQuery  MktoForms2*/
/**
 * function to smoothly scroll page to a target element
 */

function smoothScroll(targetId) {

	const targetElement = document.getElementById(targetId);

	if (targetElement) {
		const offsetTop = targetElement.offsetTop;
		window.scrollTo({
			top: offsetTop,
			behavior: "smooth"
		});
	}
}

// call page scroll events

document.querySelector(".js-goto-footer").addEventListener("click", () => smoothScroll("footerSection"));
document.querySelector(".js-goto-content").addEventListener("click", () => smoothScroll("contentSection"));

/**
 * Hero animation
 */

(function () {

	const heroSlide1 = document.querySelector(".js-hero-slide1");
	const heroSlide2 = document.querySelector(".js-hero-slide2");

	// animate blurry cloud
	anime({
		targets: ".js-cloud",
		translateY: [
			{ value: ["-40%","-70%"], delay: 1000 },
			{ value: "-100%", delay: 2100 }
		],
		translateX: ["-50%","-50%"],
		opacity: [
			{ value: 1, delay: 1000 },
			{ value: 0, delay: 2100 }
		],
		duration: 2000,
		easing: "easeOutQuart"
	});

	// bring intro heading after a delay of 1.3 seconds
	anime({
		targets: ".js-slide1-txt span",
		translateY: [
			{ value: [80, 0] },
			{ value: -80, delay: 2000}
		],
		opacity: [
			{ value: 1 },
			{ value: 0, delay: 2000}
		],
		delay: anime.stagger(100, {start: 1300}, {from: "first"}),
		easing: "easeOutQuart",
		complete: function(){
			setTimeout(() => {
				heroSlide1.classList.add("hidden");
				heroSlide2.classList.remove("hidden");

				anime({
					targets: ".js-hero-slide2 .stg",
					translateY: [100, 0],
					opacity: [0, 1],
					delay: anime.stagger(100, {start: 500}, {from: "first"}),
					easing: "easeOutQuart",
				});

				anime({
					targets: ".stg2",
					opacity: 1,
					delay: 1000,
					duration: 500,
					easing: "easeOutQuart"
				});

			}, 300);
		}
	});

})();


